.my_ach1:after {
  content: "\276f";
  padding: .5em;
  position: relative;
  right:0;
  transition:(all, .15s, linear);
  }
  .my_ach1:link ,:visited {
  text-decoration: none;
  /*font-family: $fontSans;*/
  letter-spacing: -.015em;
  font-size: .75em;
  padding: 1em;
  color: #fff;
  background: #82ca9c;
  font-weight: bold;
  border-radius: 50px;
  float: left;
  margin-left: 20%;
  width:100px;
  text-align: center;
   transition:(all, .25s, linear);
  }
.my_ach1:hover , :focus {
background: #f69679;
}
.my_ach1:after {
right: -10px;
}