img {
  max-width: 100%;
}

.d-table {
  width: 100%;
  height: 100%;
}

.d-table-cell {
  vertical-align: middle;
}

.ptb-100 {
  padding-top: 100px;
  padding-bottom: 100px;
}

.bg-gray {
  background-color: #edf5ff;
}

p {
  font-size: 16px;
  color: #818992;
  line-height: 1.6;
}

p:last-child {
  margin-bottom: 0;
}

button {
  outline: 0 !important;
}

/* btn1 btn-primary1 */
.btn-primary1 {
  color: #ffffff;
  background-color: #FF8A73;
}

.btn-primary1:hover, .btn-primary1:focus, .btn-primary1:active {
  color: #ffffff;
  background: #13c4a1;
  transform: translateY(-5px);
}

.btn-primary1.focus, .btn-primary1:focus {
  box-shadow: unset;
}

.btn-primary1:not(:disabled):not(.disabled).active:focus, .btn-primary1:not(:disabled):not(.disabled):active:focus, .btn-primary1 .show > .btn-primary1.dropdown-toggle:focus {
  box-shadow: unset;
}

.btn-primary1:not(:disabled):not(.disabled).active, .btn-primary1:not(:disabled):not(.disabled):active, .btn-primary1 .show > .btn-primary1.dropdown-toggle {
  color: #ffffff;
  background: #13c4a1;
  transform: translateY(-5px);
}

.btn-primary1.disabled, .btn-primary1:disabled {
  color: #ffffff;
  background-color: #FF8A73;
  border-color: #FF8A73;
  opacity: 1;
}

.btn-primary1.disabled:hover, .btn-primary1.disabled:focus, .btn-primary1:disabled:hover, .btn-primary1:disabled:focus {
  background: #13c4a1;
}

.btn1 {
  font-weight: 700;
  font-size: 15px;
  border: none;
  padding: 13px 31px;
  border-radius: 5px;
  text-transform: uppercase;
  position: relative;
  box-shadow: 0 13px 27px 0 rgba(163, 48, 53, 0.25);
  z-index: 1;
  transition: .9s;
}

.btn1::after, .btn1::before {
  content: '';
  position: absolute;
  top: 0;
  height: 100%;
  width: 0;
  transition: .4s;
  background: #13c4a1;
  z-index: -1;
  border-radius: 5px;
}

.btn1::after {
  left: 0;
}

.btn1::before {
  right: 0;
}

.btn1:hover::after, .btn1:hover::before {
  width: 50%;
}

/* section title */
.section-title {
  text-align: center;
  max-width: 720px;
  margin-bottom: 50px;
  margin-top: -6px;
  margin-left: auto;
  margin-right: auto;
}

.section-title h2 {
  font-size: 35px;
  font-weight: 700;
  margin-bottom: 0;
}

.section-title p {
  max-width: 650px;
  margin: 0 auto;
}

.section-title .bar {
  height: 4px;
  width: 85px;
  background: #13c4a1;
  margin: 20px auto 25px;
  position: relative;
  border-radius: 5px;
  overflow: hidden;
}

.section-title .bar::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 5px;
  background: #ffffff;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: MOVE-BG;
          animation-name: MOVE-BG;
}

@-webkit-keyframes MOVE-BG {
  from {
    -webkit-transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(85px);
  }
}

@keyframes MOVE-BG {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(85px);
  }
}

/* form-control */
.form-group {
  margin-bottom: 15px;
}

.form-control {
  height: 50px;
  padding: 12px;
  font-size: 16px;
  border: 1px solid #eeeeee;
  border-radius: 5px;
  transition: .4s;
  color: #818992;
  background: #ffffff;
