.notfound {
  box-shadow: 0 2px 48px 0 rgba(0, 0, 0, 0.08);
  background: #ffffff;
  padding: 40px;
  padding-left: 280px;
  max-width: 820px;
  margin: 0 auto;
  z-index: 1;
  position: relative;
  border-radius: 50px 5px 5px 50px;
