.notfound .notfound-404 {
  position: absolute;
  left: 0;
  top: 50%;
  display: inline-block;
  width: 140px;
  height: 140px;
  background-image: url(data:image/png;base64,);
  background-size: cover;
  margin-top: -68px;
}

.notfound .notfound-404::before {
  content: '';
  position: absolute;
  width: 100%;
