/*================================================
Default CSS
=================================================*/
body {
  padding: 0;
  margin: 0;
  font-family: "Archivo", sans-serif;
  font-size: 14px;
}

p {
  color: #666666;
  line-height: 1.8;
  font-size: 14px;
  margin-bottom: 15px;
}

p:last-child {
  margin-bottom: 0;
}

a {
  display: block;
  transition: 0.5s;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

.d-table {
  width: 100%;
  height: 100%;
}

.d-table-cell {
  vertical-align: middle;
}

.ptb-60 {
  padding-top: 60px;
  padding-bottom: 60px;
}

.pb-60 {
  padding-bottom: 60px;
}

.pt-60 {
  padding-top: 60px;
}

.h1:last-child, .h2:last-child, .h3:last-child, .h4:last-child, .h5:last-child, .h6:last-child, h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child {
  margin-bottom: 0;
}

img {
  max-width: 100%;
}

/*section-title*/
.section-title {
  margin-bottom: 40px;
  text-align: center;
  background-color: #fcfbfb;
  padding: 15px;
}

.section-title h2 {
  margin-bottom: 0;
  display: inline-block;
  font-size: 18px;
  position: relative;
  padding-left: 20px;
}

.section-title h2 .dot {
  position: absolute;
  top: 4px;
  left: 0;
  width: 12px;
  height: 12px;
  border: 1px solid #222222;
}

.section-title h2 .dot::before {
  position: absolute;
  top: 0;
  content: '';
  left: 0;
  right: 0;
  bottom: 0;
  background: #222222;
  margin: 1px;
}

.section-title.without-bg {
  padding: 0;
  background-color: transparent;
}

/*btn(primary&light)*/
.btn {
  border: 1px solid transparent;
  padding: 11px 25px;
  line-height: normal;
  line-height: initial;
  border-radius: 0;
  transition: 0.5s;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 14px;
}

.btn.disabled, .btn:disabled {
  opacity: 1;
}

.btn-primary {
  color: #ffffff;
  background-color: #222222;
  border-color: #222222;
}

.btn-primary.disabled, .btn-primary:disabled {
  color: #ffffff;
  background-color: #222222;
  border-color: #222222;
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show > .btn-primary.dropdown-toggle {
  box-shadow: unset;
  background-color: transparent;
  color: #222222;
  border-color: #222222;
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show > .btn-primary.dropdown-toggle:focus {
  box-shadow: unset;
}

.btn-primary:hover, .btn-primary:focus {
  box-shadow: unset;
  background-color: transparent;
  color: #222222;
  border-color: #222222;
}

.btn-light {
  background-color: transparent;
  color: #222222;
  border-color: #222222;
}

.btn-light.disabled, .btn-light:disabled {
  background-color: transparent;
  color: #222222;
  border-color: #222222;
}

.btn-light:not(:disabled):not(.disabled).active, .btn-light:not(:disabled):not(.disabled):active, .show > .btn-light.dropdown-toggle {
  box-shadow: unset;
  color: #ffffff;
  background-color: #222222;
  border-color: #222222;
}

.btn-light:not(:disabled):not(.disabled).active:focus, .btn-light:not(:disabled):not(.disabled):active:focus, .show > .btn-light.dropdown-toggle:focus {
  box-shadow: unset;
}

.btn-light:hover, .btn-light:focus {
  box-shadow: unset;
  color: #ffffff;
  background-color: #222222;
  border-color: #222222;
}

/*owl-carousel-btn*/
.owl-theme .owl-nav.disabled + .owl-dots {
  margin-top: 20px;
  line-height: normal;
  line-height: initial;
}

.owl-theme .owl-dots .owl-dot {
  outline: 0;
}

.owl-theme .owl-dots .owl-dot span {
  margin: 0 3px !important;
  transition: 0.5s !important;
  border-radius: 50% !important;
  width: 10px !important;
  height: 10px !important;
  background: #d2d2d2 !important;
}

.owl-theme .owl-dots .owl-dot:hover span, .owl-theme .owl-dots .owl-dot.active span {
  background: #222222 !important;
}

/*form-control*/
.form-control {
  background-color: #f5f5f5;
  width: 100%;
  height: 45px;
  padding: 0 15px;
  font-size: 14px;
  line-height: normal;
  line-height: initial;
  color: #222222;
  border: none;
  border-radius: 0;
  transition: 0.5s;
}

.form-control:focus {
  outline: 0;
  background-color: #f5f5f5;
  box-shadow: unset;
}

.form-control::-webkit-input-placeholder {
  color: #999999;
}

.form-control:-ms-input-placeholder {
  color: #999999;
}

.form-control::-ms-input-placeholder {
  color: #999999;
}

.form-control::placeholder {
  color: #999999;
}

/*================================================
Top Panel Area CSS
=================================================*/
.top-panel {
  background-color: #222222;
  text-align: center;
  position: relative;
  transition: 0.5s;
  height: auto;
  padding-top: 13px;
  padding-bottom: 13px;
}

.top-panel .panel-content {
  transition: 0.5s;
}

.top-panel .panel-content p {
  margin-bottom: 0;
  line-height: normal;
  line-height: initial;
  color: #ffffff;
  font-size: 17px;
  font-style: italic;
}

.top-panel .panel-content p b {
  font-size: 20px;
}

.top-panel .panel-content p a {
  display: inline-block;
  line-height: normal;
  line-height: initial;
  color: #ffffff;
  position: relative;
  font-weight: 700;
  font-style: normal;
}

.top-panel .panel-content p a::before {
  content: '';
  position: absolute;
  bottom: 0;
  width: 0;
  height: 2px;
  left: 0;
  transition: 0.5s;
  background: #ffffff;
}

.top-panel .panel-content p a:hover::before, .top-panel .panel-content p a:focus::before {
  width: 100%;
}

.top-panel .container {
  position: relative;
}

.top-panel .panel-close-btn {
  position: absolute;
  right: 15px;
  top: 50%;
  z-index: 2;
  transform: translateY(-50%);
  color: #ffffff;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s;
  opacity: 0.9;
}

.top-panel .panel-close-btn:hover, .top-panel .panel-close-btn:focus {
  color: #ffffff;
  opacity: 1;
}

.top-panel.hide {
  opacity: 0;
  visibility: hidden;
  height: 0;
  transition: 0.5s;
  padding: 0;
}

.top-panel.hide .panel-content {
  height: 0;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
}

/*================================================
Top Header Area CSS
=================================================*/
.top-header {
  background-color: #f1f1f1;
  padding-top: 10px;
  padding-bottom: 10px;
}

.top-header .container-fluid {
  padding-left: 35px;
  padding-right: 35px;
