/*================================================
Default CSS
=================================================*/
body {
  font-family: "Nunito", sans-serif;
  padding: 0;
  margin: 0;
}

a {
  color: #000000;
  text-decoration: none;
  transition: .4s;
  display: inline-block;
  outline: 0;
}

a:hover, a:focus {
  text-decoration: none;
  color: #EE7E0F;
}

img {
  max-width: 100%;
}

.d-table {
  width: 100%;
  height: 100%;
}

.d-table-cell {
  vertical-align: middle;
}

.ptb-100 {
  padding-top: 100px;
  padding-bottom: 100px;
}

.bg-gray {
  background-color: #edf5ff;
}

p {
  font-size: 16px;
  color: #818992;
  line-height: 1.6;
}

p:last-child {
  margin-bottom: 0;
}

button {
  outline: 0 !important;
}

/* btn btn-primary */
.btn-primary {
  color: #ffffff;
  background-color: #EE7E0F;
}

.btn-primary:hover, .btn-primary:focus, .btn-primary:active {
  color: #ffffff;
  background: #71C265;
  transform: translateY(-5px);
}

.btn-primary.focus, .btn-primary:focus {
  box-shadow: unset;
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .btn-primary .show > .btn-primary.dropdown-toggle:focus {
  box-shadow: unset;
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .btn-primary .show > .btn-primary.dropdown-toggle {
  color: #ffffff;
  background: #71C265;
  transform: translateY(-5px);
}

.btn-primary.disabled, .btn-primary:disabled {
  color: #ffffff;
  background-color: #EE7E0F;
  border-color: #EE7E0F;
  opacity: 1;
}

.btn-primary.disabled:hover, .btn-primary.disabled:focus, .btn-primary:disabled:hover, .btn-primary:disabled:focus {
  background: #71C265;
}

.btn {
  font-weight: 700;
  font-size: 15px;
  border: none;
  padding: 13px 31px;
  border-radius: 5px;
  text-transform: uppercase;
  position: relative;
  box-shadow: 0 13px 27px 0 rgba(163, 48, 53, 0.25);
  z-index: 1;
  transition: .9s;
}

.btn::after, .btn::before {
  content: '';
  position: absolute;
  top: 0;
  height: 100%;
  width: 0;
  transition: .4s;
  background: #71C265;
  z-index: -1;
  border-radius: 5px;
}

.btn::after {
  left: 0;
}

.btn::before {
  right: 0;
}

.btn:hover::after, .btn:hover::before {
  width: 50%;
}

/* section title */
.section-title {
  text-align: center;
  max-width: 720px;
  margin-bottom: 50px;
  margin-top: -6px;
  margin-left: auto;
  margin-right: auto;
}

.section-title h2 {
  font-size: 35px;
  font-weight: 700;
  margin-bottom: 0;
}

.section-title p {
  max-width: 650px;
  margin: 0 auto;
}

.section-title .bar {
  height: 4px;
  width: 85px;
  background: #71C265;
  margin: 20px auto 25px;
  position: relative;
  border-radius: 5px;
  overflow: hidden;
}

.section-title .bar::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 5px;
  background: #ffffff;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: MOVE-BG;
          animation-name: MOVE-BG;
}

@-webkit-keyframes MOVE-BG {
  from {
    -webkit-transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(85px);
  }
}

@keyframes MOVE-BG {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(85px);
  }
}

/* form-control */
.form-group {
  margin-bottom: 15px;
}

.form-control {
  height: 50px;
  padding: 12px;
  font-size: 16px;
  border: 1px solid #eeeeee;
  border-radius: 5px;
  transition: .4s;
  color: #818992;
  background: #ffffff;
}

.form-control::-webkit-input-placeholder {
  color: #818992;
}

.form-control:-ms-input-placeholder {
  color: #818992;
}

.form-control::-ms-input-placeholder {
  color: #818992;
}

.form-control::placeholder {
  color: #818992;
}

.form-control:focus {
  color: #818992;
  background-color: #ffffff;
  border-color: #71C265;
  outline: 0;
  box-shadow: unset;
}

/*================================================
Preloader CSS
=================================================*/
.preloader-area {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 999999999;
  background: #ffffff;
}

.preloader-area .lds-hourglass {
  display: inline-block;
  width: 80px;
  height: 80px;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  margin-top: -95px;
}

.preloader-area .lds-hourglass::after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 6px;
  box-sizing: border-box;
  border: 36px solid #71C265;
  border-color: #71C265 transparent #EE7E0F transparent;
  -webkit-animation: lds-hourglass 2.9s infinite;
          animation: lds-hourglass 2.9s infinite;
}

@-webkit-keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    -webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
            animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}

@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    -webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
            animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}

